import { Component, OnInit } from '@angular/core';

import { SearchService } from './search.servie';

import {} from 'rxjs/Subscriber';




@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit {

  public instaName:String;
  public instaUrl:String;
  public instaBg:String;
  public instaUser:String;
  public instaSearch:Boolean = false;
  constructor(private searchService:SearchService) { 
  	
  }

  ngOnInit() {
  	this.instaName = "";
  }

  loadBackground(){
  	  this.searchService.getImageBackground(this.instaName).subscribe( (url)=>  {
        this.instaUrl =url.insta_url;
        this.instaBg = `url(${url.insta_url})`;
        this.instaUser = url.user;
        this.instaSearch=true;
      })
      
  }

  loadToStyle(){
    if(this.instaUser ==this.instaName){
      return this.instaBg;
    }else{
      return "";
    }
  }

}



